






















import mpvueGestureLock from '../../components/mpvueGestureLock';
import uniNavBar from '../../components/uni-nav-bar.vue'
import service_pw from '../../service_pw.js' // 保存手势密码的方法
import {mapState,mapMutations} from 'vuex'

export default {
	components: {
		mpvueGestureLock,
		uniNavBar
	},
	data() {
		return {
			password: [],
		}
	},
	onShow() {
		this.getPword();
	},
	computed:mapState(['username']),
	methods: {
		...mapMutations(['']),
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		showMenu() {
			uni.showToast({
				title: '点击了注册'
			})
		},
		getPword(){
			let pword = uni.getStorageSync('lsPassword'); // 获取缓存的手势密码
			if(pword){
				this.password = pword;
			}
		},
		goMine(){
			uni.navigateBack({
				delta:1
			})
		},
		showTip(val){
			uni.showToast({
				icon:'none',
				title:`${val}`,
				duration:500
			});
		},
		onEnd(data) {
			if(this.password.length){
				if (this.password.join('') === data.join('')) {
					this.showTip('手势设定完成');
					uni.setStorageSync('lsPassword',this.password);
					this.goMine();
				}else{
					this.showTip('两次手势不匹配');
					this.password = []; // 清空手势记录，重来
				}
			}else {
				this.showTip('请确认手势设定');
				this.password = data;
			}
		}
	}
}



